@mixin mob320 {
  @media (max-width: 320px) { @content };
}
@mixin mob375 {
  @media (max-width: 375px) { @content };
}
@mixin mob425 {
  @media (max-width: 425px) { @content };
}
@mixin mob480 {
  @media (max-width: 480px) { @content };
}

@mixin Xs {
  @media (max-width: 767px) {@content};
}
//For Small
@mixin Sm {
  @media (max-width: 991px) {@content};
}
//For Medium
@mixin Md {
  @media (min-width: 992px) {@content;}
}
//For Extra Large
@mixin Lg {
  @media (min-width: 1200px) {@content;}
}


// Animations
@keyframes shine{
  from{
    left: -50px;
  }
  to{
    left: calc(100% + 50px);
  }
}

@keyframes over{
  0%{
    opacity: 1
  }
  25%{
    opacity: .5
  }
  50%{
    opacity: 1
  }
  75%{
    opacity: .5
  }
  100%{
    opacity: 1
  }
}