@import 'import';

// Lang

[dir="rtl"] body{
    direction: rtl;
    text-align: right
}

body{
    font-family: "reg";
    background-color: #EAEBE5;
}

.over{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #EAEBE5;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999999;
    
    img{
        animation: over 3s infinite linear;
    }
}

// Search
.main-search{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.98);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    transition: .5s;
    opacity: 0;
    visibility: hidden;

    &.open{
        visibility: visible;
        opacity: 1;
    }

    > i {
        position: absolute;
        top: 20%;
        left: 50%;
        transform: translateX(-50%);
        font-size: 25px;
        color: #587681;
        cursor: pointer;
        transition: .5s;

        &:hover{
            color: #FFF;
        }
    }

    .content{
        margin: auto;
        width: 70%;
        position: relative;

        @include Sm{
            width: 95%;
        }

        input{
            width: 100%;
            border: 0;
            background-color: transparent;
            height: 50px;
            color: #587681;
            display: block;
            border-radius: 100px;
            padding: 0 25px;
            border: 2px solid #587681;
            transition: .5s;

            &::placeholder{
                color: rgba(255,255,255, .5);
            }
            
            &:focus{
                outline: none;
                border: 2px solid #fff;
            }
        }
        
        button{
            width: 100px;
            height: 40px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 5px;
            border-radius: 100px;
            cursor: pointer;
            border: 0;
            background-color: #587681;
            color: #FFF;
            font-family: "med";
            font-size: 15px;
            transition: .5s;

            [dir="ltr"] & {
                left: unset;
                right: 5px;
            }

            &:hover{
                background-color: #FFF;
                color: #587681
            }
        }
    }
}
// Search

// Navbar Side

.navbar-side{
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 999999;
    opacity: 0;
    visibility: hidden;
    transition: .5s;

    ul{
        width: 260px;
        height: 100vh;
        background-color: #FFF;
        margin: 0;
        padding: 30px 20px;
        transform: translateX(100%);
        transition: .5s;

        [dir="ltr"] & {
            transform: translateX(-100%);
        }

        i{
            float: left;
            font-size: 20px;
            color: #587681;
            cursor: pointer;

            [dir="ltr"] & {
                float: right
            }
        }

        li{
            padding: 5px 0;
            &:not(:last-child){
                border-bottom: 1px solid #58768170;
            }

            &:first-of-type{
                margin-top: 70px;
            }

            a{
                font-size: 18px;
                color: #587681;
                text-decoration: none;
                transition: .5s;

                &:hover{
                    color: #F4AE32
                }
            }
        }
    }

    &.open{

        opacity: 1;
        visibility: visible;
        
        ul{
            transform: translateX(0);
        }
    }
}

// Navbar Side

// Header
.header{
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: cover;
    border-bottom: 4px solid #F4AE32;

    > .container {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        @include Sm{
            transform: translate(-50%, -20%);
        }
    }

    .topNav{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 999;

        .content{
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    .right{
        background-color: #EAEBE5;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;

        &:before{
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 700px;
            height: 100%;
            background-color: #EAEBE5;
            -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
            box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
            z-index: -1;

            [dir="ltr"] & {
                right: 0;
                left: unset;
            }
        }

        &:after{
            content: url("../images/sh.png");
            position: absolute;
            top: 0;
            left: -70px;

            [dir="ltr"] & {
                right: -70px;
                left: unset;
                transform: scaleX(-1);
            }

            @include mob375{
                content: "";
                position: absolute;
                top: 0px;
                left: -47px;
                height: 103.49px;
                width: 50px;
                background-image: url(../images/sh.png);
                background-size: contain;

                [dir="ltr"] & {
                    right: -47px;
                    left: unset;
                }
            }
        }

        .logo{

            img{

                @include mob375{
                    width: 110px;
                }
            }
        }
        
        .bars{
            width: 33px;
            height: 22px;
            cursor: pointer;

            span{
                display: flex;
                width: 100%;
                height: 2px;
                background-color: #587681;
                position: relative;

                &:after{
                    content: "";
                    position: absolute;
                    top: 20px;
                    left: 0;
                    display: block;
                    width: 100%;
                    height: 2px;
                    background-color: #587681
                }

                &:before{
                    content: "";
                    position: absolute;
                    top: 10px;
                    left: 0;
                    display: block;
                    width: 100%;
                    height: 2px;
                    background-color: #587681
                }
            }
        }
    }

    .left{
        display: flex;
        align-items: center;

        .lang{
            position: relative;
            padding-left: 20px;
            border-left: 1px solid #FFF;
            [dir="ltr"] &{
                padding-right: 20px;
                padding-left: 0;
                border-right: 1px solid #FFF;
                border-left: 0;
            }
            i{
                font-size: 12px;
                color: #FFF;
                margin-left: 10px;

                [dir="ltr"] &{
                    margin-right: 10px;
                    margin-left: 0;
                }
            }

            span{
                font-size: 14px;
                color: #FFF;
                font-family: "gotham";
            }

            .dropDown{
                position: absolute;
                top: 100%;
                left: 50%;
                transform: translate(-50%, 20px);
                padding: 10px;
                background-color: #FFF;
                width: 80px;
                margin-bottom: 0;
                border-radius: 5px;
                box-shadow: 0 0 10px rgba(0, 0, 0, 0.07);
                opacity: 0;
                visibility: hidden;

                transition: .5s;

                &:after{
                    content: "";
                    width: 0;
                    height: 0;
                    border: 5px solid transparent;
                    border-bottom: 6px solid #FFF;
                    position: absolute;
                    top: -11px;
                    left: 50%;
                    transform: translateX(-50%);
                }

                li{
                    text-align: center;
                    &:not(:last-child){
                        border-bottom: 1px  solid rgba(0, 0, 0, 0.07)
                    }

                    a{
                        font-size: 14px;
                        font-family: "gotham";
                        color: #587681;
                        transition: .5s;

                        &:hover{
                            text-decoration: none;
                            color: #F4AE32
                        }
                    }
                }
            }

            &:hover{
                .dropDown{
                    transform: translate(-50%, 10px);
                    opacity: 1;
                    visibility: visible;
                }
            }
        }

        .search{
            padding-right: 20px;
            cursor: pointer;

            [dir="ltr"] &{
                padding-left: 20px;
                padding-right: 0;
            }
        }
    }

    .swiper-container{

        .content{
            direction: rtl;
            text-align: right;
            width: 53%;
            margin: auto;

            @include Sm{
                width: 100%;
            }

            [dir="ltr"] & {
                direction: ltr;
                text-align: left;
            }

            p{
                font-size: 25px;
                font-family: "reg";
                color: #FFF;
                margin-bottom: 14px;

                @include Xs{
                    font-size: 20px;
                }
            }
            
            h2{
                font-size: 40px;
                font-family: "bold";
                color: #FFF;
                margin-bottom: 45px;

                @include Xs{
                    font-size: 40px;
                }

                @include Xs{
                    font-size: 30px;
                }
            }

            a{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 180px;
                height: 51px;
                border: 1px solid #FFF;
                font-size: 14px;
                font-family: "bold";
                color: #FFF;
                border-radius: 7px;
                transition: .5s;

                img{
                    margin-left: 5px;

                    [dir="ltr"] & {
                        margin-right: 5px;
                        margin-left: 0;
                    }
                }

                &:hover{
                    background-color: #587681;
                    text-decoration: none;
                    border: 1px solid #587681;
                }
            }
        }

        .swiper-pagination{
            position: absolute;
            top: 50%;
            right: -50%;
            left: unset;
            transform: rotate(90deg) translateY(100%);
            height: 10px;

            @include Sm{
                position: unset;
                top: unset;
                right: unset;
                left: unset;
                transform: unset;
                height: 33px;
                margin-top: 80px;
            }

            // @include Xs{
            //     margin-top: 30px;
            // }

            > *{
                transform: rotate(-90deg);
            }

            [dir="ltr"] & {
                right: unset;
                left: -50%;
                transform: rotate(90deg) translateY(-270%);

                @include Xs{
                    transform: none;
                }
            }

            span{
                margin: 0 20px;
                width: 6px;
                height: 6px;
                background-color: #FFF;
                opacity: 1 !important;
                position: relative;
                transition: .5s;

                &:after{
                    content: "";
                    width: 36px;
                    height: 36px;
                    border-radius: 50%;
                    border: 2px solid #FFF;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: scale(.9) translate(-50%, -50%);
                    opacity: 0;
                    visibility: hidden;
                    transition: .5s;

                    @include Sm{
                        width: 26px;
                        height: 26px;
                    }
                }

                &.swiper-pagination-bullet-active{
                    width: 8px;
                    height: 8px;
                    &:after{
                        opacity: 1;
                        visibility: visible;
                        transform: scale(1) translate(-50%, -50%);
                    }

                    p{
                        transform: translateY(-50%) translateX(0px);
                        opacity: 1;
                        visibility: visible;
                    }
                }

                &:focus{
                    outline: none;
                    box-shadow: none;
                }

                p{
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%) translateX(10px);
                    // transform: rotate(-90deg);
                    right: 37px;
                    width: max-content;
                    margin: 0;
                    display: flex;
                    align-items: center;
                    flex-direction: row-reverse;
                    font-size: 12px;
                    font-family: "reg";
                    color: #FFF;
                    transition: .5s;
                    opacity: 0;
                    visibility: hidden;

                    [dir="ltr"] & {
                        left: 37px;
                        right: unset;
                    }

                    [dir="ltr"] & {
                        display: none;
                    }

                    &:after{
                        content: "";
                        display: block;
                        width: 30px;
                        height: 1px;
                        background-color: #FFF;
                        margin-left: 13px;

                        [dir="ltr"] & {
                            margin-left: 0;
                            margin-right: 13px;
                        }
                    }
                }
            }
        }
    }
}
