// App

.app{
    background-image: linear-gradient(0deg, #254C5B 0%, #6F8A93 100%, #BBC9CC 100%, #BBC9CC 100%);
    padding: 84px 0 45px;

    @include Sm{
        overflow-x: hidden;
    }

    .col-lg-6{

        @include Sm{
            &:last-child{
                order: -1
            }
        }


        > h2 {
            font-size: 30px;
            color: #FFF;
            font-weight: bold;
            display: none;
            @include Sm{
                display: block;
                margin-bottom: 40px;
                font-size: 25px;
            }

            + img{

                @include Sm{
                    width: 450px;
                    display: block;
                    margin: auto;
                }
            }
        }
    }

    .show{

        h2{
            font-size: 30px;
            color: #FFF;
            font-weight: bold;
            margin-bottom: 90px;

            @include Sm{
                display: none;
                margin-bottom: 40px;
                font-size: 25px;
            }
        }

        .details{

            .detail{
                display: flex;
                align-items: center;
                margin-bottom: 45px;

                .img{
                    width: 94px;
                    height: 94px;
                    border-radius: 50%;
                    background-color: #FFF;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-left: 33px;

                    [dir="ltr"] & {
                        margin-left: 0;
                        margin-right: 33px;

                        @include Xs{
                            margin-right: 0;
                        }
                    }

                    @include Xs{
                        margin-right: 0;
                        margin-left: 0;
                        margin-bottom: 20px;
                    }

                    @include mob480{
                        margin-right: 0;
                        margin-left: 0;
                        margin-bottom: 20px;
                    }
                }

                &:nth-child(1){
                    transform: translateX(-90px);
                    [dir="ltr"] & {
                        transform: translateX(90px);
                    }
                }

                &:nth-child(2){
                    transform: translateX(-22px);
                    [dir="ltr"] & {
                        transform: translateX(22px);
                    }
                    .img{
                        background-color: #AAE8FF
                    }
                }
                &:nth-child(3){
                    transform: translateX(16px);
                    [dir="ltr"] & {
                        transform: translateX(-16px);
                    }
                    .img{
                        background-color: #E16F64
                    }
                }
                &:nth-child(4){
                    transform: translateX(-22px);
                    [dir="ltr"] & {
                        transform: translateX(22px);
                    }
                    .img{
                        background-color: #4C6C77
                    }
                }
                &:nth-child(5){
                    transform: translateX(-90px);
                    [dir="ltr"] & {
                        transform: translateX(90px);
                    }
                    .img{
                        background-color: #117A68
                    }
                }

                @include Xs{
                    transform: none !important;
                    flex-direction: column;
                    text-align: center;
                }

                @include mob480{
                    flex-direction: column;
                    text-align: center;
                }

                .box{

                    p{
                        font-size: 25px;
                        color: #FFF;
                        font-family: med;
                        margin-bottom: 5px;
                    }

                    span{
                        font-size: 14px;
                        font-family: "light";
                        color: #E6E1E1;
                    }
                }
            }
        }
    }
}

