        }
    }

    .swiper-pagination{
        position: unset;
        margin-top: 25px;
        display: flex;
        justify-content: center;

        span{
            display: block;
            width: 97px;
            height: 31px;
            border-bottom: 2px solid #ADB5B7;
            margin: 0 1.5px !important;
            background-color: transparent;
            border-radius: 0;
            opacity: 1;
            font-size: 13px;
            font-family: reg;
            color: #587681;
            transition: .5s;

            &.swiper-pagination-bullet-active{
                border-bottom: 2px solid #758F97;
                font-size: 14px;
                font-family: bold;
            }
        }
    }
}

