// Faq

.faq{
    padding: 87px 0 58px;
    background-color: #FFF;

    @include Sm{
        padding: 40px 0;
    }

    h2{
        font-size: 30px;
        font-family: bold;
        color: #587681;
        margin-bottom: 85px;

        @include Sm{
            margin-bottom: 40px;
            font-size: 25px;
        }
    }

    .accordion{
        
        .card{
            margin-bottom: 14px;
            border-radius: 0;
            border: 0;

            .card-header{
                padding: 0;

            }
            
            button{
                position: relative;
                background-image: linear-gradient(0deg, #254C5B 0%, #6F8A93 100%, #BBC9CC 100%, #BBC9CC 100%);
                padding: 0 50px;
                height: 95px;
                font-size: 26px;
                color: #FFF;
                font-family: med;
                text-decoration: none;
                text-align: start;
                border: none;
                border-radius: 0;

                @include mob480{
                    font-size: 18.8px;
                }
                
                &:focus{
                    box-shadow: none;
                }
                
                i{
                    color: #FFF;
                    margin-left: 20px;
                    font-size: 16px;
                    transform: rotate(180deg);

                    [dir="ltr"] & {
                        margin-right: 20px;
                        margin-left: 0;
                    }
                }
                
                &.collapsed{
                    border: 1px solid #F8D9A4;
                    background: none;
                    border-radius: 7px;
                    background-color: #FFF;
                    color: #3B5E6C;
                    
                    i{
                        transform: rotate(0);
                        color: #3B5E6C;
                    }
                    
                    &:after, &:before{
                        display: none;
                    }
                }
                
                &:after{
                    content: "";
                    display: block;
                    width: 52px;
                    height: 74px;
                    background-image: url("../images/shape.png");
                    background-size: cover;
                    position: absolute;
                    top: 0;
                    right: 0;
                    z-index: 999;
                    filter: brightness(0.5);
                    
                    [dir="ltr"] & {
                        top: unset;
                        right: unset;
                        bottom: 0;
                        left: 0;
                    }
                }
    
                &:before{
                    content: "";
                    display: block;
                    width: 74px;
                    height: 105px;
                    background-image: url("../images/shape.png");
                    background-size: cover;
                    position: absolute;
                    bottom: 0;
                    left: 22px;
                    z-index: 999;
                    
                    [dir="ltr"] & {
                        bottom: unset;
                        left: unset;
                        top: 0;
                        right: 22px;
                    }
                }

                &:hover{
                    text-decoration: none;
                }
            }

            .card-body{
                padding: 40px 50px;
                font-size: 20px;
                font-family: med;
                color: #393F41;
                line-height: 55px;
                border: 1px solid #E3E3E3 !important;

                @include mob375{
                    line-height: 1.6;
                    text-align: justify
                }
            }
        }
    }
}

