// Footer

footer{
    padding: 75px 0 40px;
    background-image: linear-gradient(to bottom, #254C5B 0%, #6F8A93 100%, #BBC9CC 100%, #BBC9CC 100%);
    
    @include mob480{
        text-align: center;
    }
    
    .details{
        .logo{
            margin-bottom: 30px;
        }

        .content{
            width: 350px;
            font-size: 12px;
            line-height: 30px;
            font-family: light;
            color: #FFF;

            @include Sm{
                width: auto;
                margin-bottom: 40px;
            }
        }
    }
    
    .head{
        margin-bottom: 34px;
        font-size: 14px;
        font-family: bold;
        color: #FFF;
    }

    .links{

        ul{
            padding: 0;
            margin: 0;

            li{
                margin-bottom: 15px;
                display: flex;
                align-items: center;

                @include mob480{
                    justify-content: center;
                }
                
                i{
                    font-size: 12px;
                    color: #FFF;
                    margin-left: 12px;
                    transition: .5s;

                    [dir='ltr'] & {
                        margin-right: 12px;
                        margin-left: 0;
                    }
                }
    
                a{
                    font-size: 12px;
                    font-family: light;
                    color: #FFF;
                    text-decoration: none;
                    display: block;
                    border-bottom: 1px solid transparent;
                    transition: .5s;
                }

                &:hover{
                    i{
                        color: #F8A822;
                    }
                    a{
                        color: #F8A822;
                    }
                }
            }

        }
    }

    .download{

        a{
            position: relative;
            display: block;
            width: max-content;
            height: 100%;
            overflow: hidden;
            &:not(:last-child){
                margin-bottom: 12px;
            }

            @include mob480{
                margin-right: auto;
                margin-left: auto;
            }

            &:after{
                content: "";
                width: 20px;
                height: 200%;
                position: absolute;
                top: 50%;
                -webkit-transform: translateY(-50%);
                transform: translateY(-50%) rotate(45deg);
                left: -50px;
                background-color: rgba(255, 255, 255, 0.15);
                pointer-events: none;
                transition: .5s;
            }

            &:hover{

                &:after{
                    animation: shine 0.7s ease forwards;
                }
            }
        }

        img{
            width: 125px;
            height: 40px;
        }
    }

    .contact{

        @include mob480 {
            margin-top: 25px;
        }

        .support{
            display: flex;
            justify-content: center;
            flex-direction: column;

            a{
                font-size: 12px;
                font-family: light;
                color: #FFF;
                text-decoration: none;
                transition: .5s;

                &:not(:last-child){
                    margin-bottom: 25px;
                }

                img{
                    margin-left: 9px;

                    [dir="ltr"] & {
                        margin-right: 9px;
                        margin-left: 0;
                    }
                }

                &:hover{
                    color: #F8A822;
                }
            }
        }

        .social{
            margin-top: 40px;

            a{
                &:first-child{
                    margin-left: 10px;

                    [dir="ltr"] & {
                        margin-right: 10px;
                        margin-left: 0;
                    }
                }
            }
        }
    }
}

