//  End

.end{
    padding: 34px 0;
    background-color: #355A67;

    .content{
        display: flex;
        align-items: center;
        justify-content: space-between;

        .right{

            a{
                font-size: 10px;
                font-family: reg;
                color: #FFF;
                text-decoration: none;
                transition: .5s;

                &:hover{
                    color: #F8A822
                }

                &:first-child{
                    padding-left: 10px;
                    border-left: 1px solid #FFF;
                    
                    [dir="ltr"] & {
                        padding-right: 10px;
                        padding-left: 0;
