                        border-left: 0;
                    }
                }

                &:last-child{
                    padding-right: 10px;

                    [dir="ltr"] & {
                        padding-right: 0;
                        padding-left: 10px;
                    }
                }
            }
        }

        .left{
            font-size: 10px;
            font-family: reg;
            color: #FFF;
        }
    }
